import playerAPI from '../../../core/player-api-decorator';

import { thumbnailsTemplate } from './templates';

import htmlToElement from '../core/htmlToElement';
import getElementByHook from '../core/getElementByHook';

import { IRootContainer } from '../../root-container/types';
import { IPlaybackEngine } from '../../playback-engine/types';

import styleNames from './thumbnails.scss';
export default class Thumbnails {
  static moduleName = 'thumbnails';
  static dependencies = ['rootContainer', 'engine'];

  private _engine: IPlaybackEngine;

  private _config: any;
  private _node: HTMLElement;
  private _lowLoader: HTMLImageElement;
  private _highLoader: HTMLImageElement;
  private _textBlock: HTMLDivElement;
  private _node_low: any;
  private _node_high: any;
  private _lowQualityLoading: boolean;
  private _highQualityLoading: boolean;
  private _currentConfig: any;

  constructor({
    rootContainer,
    engine,
  }: {
    rootContainer: IRootContainer;
    engine: IPlaybackEngine;
  }) {
    this._engine = engine;

    this._bindCallbacks();
    this._initUI();
    rootContainer.appendComponentNode(this.node);
  }

  private _initUI() {
    this._node = htmlToElement(
      thumbnailsTemplate({
        styles: styleNames,
      }),
    );

    this._lowLoader = getElementByHook(
      this._node,
      'low-quality-loader',
    ) as HTMLImageElement;
    this._highLoader = getElementByHook(
      this._node,
      'high-quality-loader',
    ) as HTMLImageElement;

    this._textBlock = getElementByHook(
      this._node,
      'thumb-text-block',
    ) as HTMLDivElement;

    this._node_high = getElementByHook(this._node, 'high-quality-thumb');
    this._node_low = getElementByHook(this._node, 'low-quality-thumb');
  }

  private _bindCallbacks() {
    this._onLowQualityLoad = this._onLowQualityLoad.bind(this);
    this._onHighQualityLoad = this._onHighQualityLoad.bind(this);
  }

  get node(): HTMLElement {
    return this._node;
  }

  @playerAPI()
  setConfig(config: any) {
    this._config = config;
  }

  @playerAPI()
  getAt(second: number) {
    const duration = this._engine.getDurationTime();
    if (!duration) {
      return;
    }

    const playedPercent = second / duration;
    const { framesCount } = this._config;
    const neededFrame = Math.round(framesCount * playedPercent);

    return this._config.qualities.map((quality: any) => {
      const framesInSprite =
        quality.framesInSprite.vert * quality.framesInSprite.horz;
      const frameNumberInSprite = neededFrame % framesInSprite;
      const spriteNumber = Math.floor(neededFrame / framesInSprite);
      const horzPositionInSprite =
        frameNumberInSprite % quality.framesInSprite.horz;
      const vertPositionInSprite = Math.floor(
        frameNumberInSprite / quality.framesInSprite.vert,
      );
      const url = `https://storage.googleapis.com/video-player-media-server-static/thumbnails/${quality.spriteNameMask.replace(
        '%d',
        spriteNumber,
      )}`;

      return {
        frameSize: quality.frameSize,
        framesInSprite:
          (spriteNumber + 1) * framesInSprite <= framesCount
            ? quality.framesInSprite
            : {
                horz: Math.min(
                  framesCount % framesInSprite,
                  quality.framesInSprite.horz,
                ),
                vert: Math.ceil(
                  (framesCount % framesInSprite) / quality.framesInSprite.vert,
                ),
              },
        framePositionInSprite: {
          vert: vertPositionInSprite,
          horz: horzPositionInSprite,
        },
        spriteUrl: url,
      };
    });
  }

  @playerAPI()
  showAt(second: number) {
    const config = this.getAt(second);
    if (!config) {
      return;
    }
    this._currentConfig = config;

    this._checkHighQuality();
    this._checkLowQuality();
  }
  private _checkLowQuality() {
    if (this._lowLoader.src === this._currentConfig[0].spriteUrl) {
      if (!this._lowQualityLoading) {
        this._applyLowQuality();
      }
    } else {
      this._node_low.style.background = '';
      this._loadLowQuality();
    }
  }

  private _checkHighQuality() {
    if (this._highLoader.src === this._currentConfig[1].spriteUrl) {
      if (!this._highQualityLoading) {
        this._applyHighQuality();
      }
    } else {
      this._node_high.style.background = '';
      this._loadHighQuality();
    }
  }

  private _onLowQualityLoad() {
    this._lowQualityLoading = false;
    this._applyLowQuality();
  }

  private _onHighQualityLoad() {
    this._highQualityLoading = false;
    this._applyHighQuality();
  }

  private _loadLowQuality() {
    this._lowLoader.onload = this._onLowQualityLoad;
    this._lowLoader.src = this._currentConfig[0].spriteUrl;
    this._lowQualityLoading = true;
  }

  private _loadHighQuality() {
    this._highLoader.onload = this._onHighQualityLoad;
    this._highLoader.src = this._currentConfig[1].spriteUrl;
    this._highQualityLoading = true;
  }

  private _applyLowQuality() {
    this._applyQualityToNode(this._node_low, this._currentConfig[0]);
  }

  private _applyHighQuality() {
    this._applyQualityToNode(this._node_high, this._currentConfig[1]);
  }

  private _applyQualityToNode(node: HTMLElement, quality: any) {
    const viewWidth = node.offsetWidth;
    const viewHeight = node.offsetHeight;

    const backgroudWidth = viewWidth * quality.framesInSprite.horz;
    const backgroundHeight = viewHeight * quality.framesInSprite.vert;

    node.style.background = `url('${quality.spriteUrl}') -${viewWidth *
      quality.framePositionInSprite.horz}px -${viewHeight *
      quality.framePositionInSprite
        .vert}px / ${backgroudWidth}px ${backgroundHeight}px`;
  }

  setTime(time: string) {
    this._textBlock.innerText = time;
  }

  showAtPercent(percent: number) {
    this.showAt(percent / 100 * this._engine.getDurationTime());
  }

  clear() {
    this._node.style.background = '';
  }

  destroy(): void {
    this._engine = null;
  }
}
